import React from 'react';
import './TempleteWrapper.css';

import ProgressBar from './Contents/3DProgressBar.js';
import GradientDetails from './Contents/GradientDetails.js';
import CreativeBoxBorder from './Contents/CreativeBoxBorder.js';
import ListItemEffects from './Contents/ListItemEffects.js';
import TextAnimation from './Contents/TextAnimation.js';



class TempleteWrapper extends React.Component{
  render(){
    return(
      <div className="base">
        <GettingContent id = {this.props.match.params.id} />
      </div>
    );
  }
}

 function GettingContent(props){
   if(props.id === "3drepresation"){return <ProgressBar />;}
   else if(props.id === "gradientdetails"){return <GradientDetails />;}
   else if(props.id === "creativeboxborder"){return <CreativeBoxBorder />;}
   else if(props.id === "listitemeffects"){return <ListItemEffects />;}
   else if(props.id === "textanimation"){return <TextAnimation />;}
   return <div></div>
 }

export default TempleteWrapper;
