import React from 'react';
import Header from './Compornent/Header.js';
import FlexWrapper from './Compornent/FlexWrapper.js';
import './Home.css';

function Home(props) {
  return (
    <div className = "homebody">
      <Header />
      <FlexWrapper contents = {props.contents} className = "wrapper" />
    </div>
  );
}

export default Home;
