import React from 'react';
import { Link } from 'react-router-dom'
import './FlexElement.css';

class FlexElement extends React.Component{
  render(){
    return(
        <div className = "box">
          <Link to = {'/templete/' + this.props.id} className = "templetelink">
            <img src = {this.props.img_src} alt = {this.props.title}  className="flexname"/>
            <h2 className = "flextitle">{this.props.title}</h2>
            <p className = "flextext">{this.props.text}</p>
          </Link>
        </div>
      );
  }
}

export default FlexElement;
