import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from './Home.js';
import Templete from './Templetes/TempleteWrapper.js'

const CONTENTS = [
  {id: "3drepresation", img_src: require('./imgs/3DRepresation.PNG'), title: "3D_Represation",
  text: "3Dのプログレスバー"},
  {id: "gradientdetails", img_src: require('./imgs/Gradient_Details.PNG'), title: "Gradient_Details",
  text: "ちょっとおしゃれなクロージャーウィジェット。ホバーでネオンに光る挙動を入れてみた。beforeとafterの要素にぼかしを加える。"},
  {id: "creativeboxborder", img_src: require('./imgs/ListMenue.PNG'), title: "CreativeBoxBorder",
  text: "テキストカードの裏に変形させた図形を表示するコンポーネント。テキストカードの要素の:beforeに変形した図形を記述する。"},
  {id: "listitemeffects", img_src: require('./imgs/Navigation.PNG'), title: "ListItemEffects",
  text: "ナビゲーションにホバーした項目を光らせるアニメーション"},
  {id: "textanimation", img_src: require('./imgs/TextAnimation.PNG'), title: "Text_Animation",
  text: "文字の後ろに絶え間なく図形が出現するアニメーション。文字の領域に色、大きさ、位置をランダムで決定した要素を大量にjavascriptで配置している。"},
];

function App(){
  return(
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Route exact path = '/' render = {(props) => <Home contents = {CONTENTS} />} />
      <Route path = '/templete/:id' component = {Templete}  />
    </BrowserRouter>
  );
}
export default App;
