import React from 'react';
import Chips from '../Templetes/Contents/TextAnimationChips.js'
import './Header.css'

class Header extends React.Component{
  constructor() {
    super();
    this.title_text = "Templetes"
  }
  render(){
    return(
      <header className="homeheader" id="base">
        <h1 className="homeheadertext">{this.title_text}</h1>
        <Chips num_chip={50} txt_width={5} txt_height={5} />
      </header>
    );
  }
}

export default Header;
