import React from 'react';
import Chips from './TextAnimationChips.js';
import './TextAnimation.css';

class TextAnimation extends React.Component{
  render(){
    return(
      <div className="textwrapper" id="base">
        <h1>テキスト</h1>
        <Chips num_chip={50} txt_width={3} txt_height={3} />
      </div>
    );
  }
}

export default TextAnimation;
