import React from 'react';
import FlexElement from './FlexElement.js'
import './FlexWrapper.css';

class FlexWrapper extends React.Component{

  render(){
    let elements = [];

    this.props.contents.forEach((content) => {
      elements.push(
         <FlexElement
          id = {content.id}
          img_src = {content.img_src}
          title = {content.title}
          text = {content.text}
          />
      );
    })
    return(
      <div className = "wrapper">
        {elements}
      </div>
    );
  }
}

export default FlexWrapper;
